@import url("https://fonts.googleapis.com/css2?family=Source+Sans+Pro:wght@400;700&display=swap");
* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.bold {
  font-weight: 700;
}

.App {
  font-family: "Source sans pro";
  text-align: center;
  margin: 0 auto;
  width: 1440px;
}

.Header {
  height: 80px;
  display: flex;
  color: #616161;
  justify-content: space-between;
  align-items: center;
  padding: 40px 50px;
}

.logo-img {
  height: 39px;
  width: 36px;
}

.User {
  display: flex;
  align-items: center;
  font-size: 24px;
  position: relative;
}

.btn-header {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 0 12px;
  height: 48px;
  border-radius: 61px;
  background-color: #ededed;
  color: #616161;
  font-size: 24px;
  font-weight: 400;
  outline: none;
  border: none;
  cursor: pointer;
  font-family: "Source sans pro";
  margin-left: 25px;
  position: relative;
}

.Coin-img {
  margin-left: 4px;
  width: 24px;
}

.Arrow-img {
  margin-left: 4px;
  width: 15px;
}

.Account {
  position: absolute;
  top: 47px;
  right: 50%;
  width: 150px;
  margin-right: -75px;
  background: white;
  padding: 20px;
  border: none;
  border-radius: 6px;
  z-index: 999;
  overflow: auto;
  height: auto;
  max-height: 250px;
  font-size: 16px;
}

.account-title {
  font-size: 20px;
  border-bottom: 1px solid #d9d9d9;
  margin-bottom: 10px;
}

.account-btn {
  display: flex;
  flex-direction: column;
}

.account-btn div {
  display: flex;
  justify-content: center;
  font-size: 20px;
  margin-bottom: 10px;
  padding: 3px;
  outline: none;
  border: none;
  border-radius: 6px;
  color: #616161;
  background-color: #ededed;
}

.Main {
  width: 100%;
  height: 480px;
  margin-bottom: 80px;
  position: relative;
}

.Main img {
  max-width: 100%;
}

.Main p {
  font-size: 64px;
  font-weight: 700;
  color: #ffffff;
  position: absolute;
  left: 100px;
  top: 300px;
}

.Filter {
  height: 70px;
  width: 1176px;
  font-size: 24px;
  display: flex;
  justify-content: space-between;
  margin: 0 130px;
  border-bottom: solid 1px #d9d9d9;
}

.Filter-text {
  display: flex;
}

.Filter-number {
  display: flex;
  align-items: center;
  padding-right: 20px;
  color: #616161;
  font-size: 24px;
}

.Filter-btn {
  display: flex;
  align-items: center;
  border-left: solid 1px #d9d9d9;
  color: #a3a3a3;
  padding-left: 20px;
}

.Filter-btn div {
  margin-right: 20px;
}

.Filter button {
  height: 48px;
  width: 163px;
  border-radius: 20.5px;
  background-color: #ededed;
  outline: none;
  border: none;
  font-size: 18px;
  color: #a3a3a3;
  margin-right: 20px;
  cursor: pointer;
}

.Filter button.active {
  background-color: #1ed1f2;
  color: white;
  transition: all 0.5s ease;
}

.Filter-select {
  height: 48px;
  width: 193px;
  border-radius: 20.5px;
  background-color: #ededed;
  outline: none;
  border: none;
  font-size: 18px;
  color: #a3a3a3;
  margin-right: 20px;
  cursor: pointer;
}

.Filter-arrows{
  display: flex;
}

.Filter-arrows img {
  padding: 10px;
  cursor: pointer;
}

.Product {
  width: 23%;
  height: 276px;
  background-color: #ffffff;
  margin: 1%;
  box-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
  position: relative;
}

.product-description {
  display: flex;
  flex-direction: column;
  width: 228px;
  text-align: left;
}

.product-img {
  border-bottom: 1px solid #d9d9d9;
  width: 252px;
  height: 182px;
}

.product-img-blue{
  position:absolute;
  top: 15px;
  left: 220px;
}

.product-category {
  padding-left: 10px;
  font-size: 16px;
  color: #a3a3a3;
}

.product-name {
  padding-left: 10px;
  font-size: 18px;
  color: #616161;
}

.product-buy{
  height: 42px;
  width: auto;
  background-color: #a3a3a3c9;
  border-radius: 36px;
  color: white;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  font-size: 14px;
  padding: 15px;
  position:absolute;
  top: 15px;
  left: 120px;
}

.product-modal{
  height: 100%;
  width: 100%;
  background-color: #0ad2fac7;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 999;
  display: flex;
  justify-content: center;
  align-items: center;
}

.product-modal-img {
  position:absolute;
  top: 15px;
  left: 220px;
}

.product-modal-points {
  display: flex;
  justify-content: center;
  align-items: center;
}

.product-modal-points h1 {
  color: white;
  font-size: 36px;
}

.Coin-modal-img {
  margin-left: 7px;
  width: 35px;
}

.product-modal-btn {
  height: 48px;
  width: 180px;
  border-radius: 20.5px;
  background-color: white;
  outline: none;
  border: none;
  font-size: 18px;
  margin-top: 20px;
  cursor: pointer;
  position: relative;
}

.loading img {
  position: absolute;
  top: 10px;
  right: 80px;
}

.List {
  padding: 40px 0;
  width: 1176px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin: 0 auto;
}

.modal{
  height: 100%;
  width: 100%;
  background-color: #4d5656ad;
  position: fixed;
  top: 0;
  left: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 999;

}

.message {
   width: 450px;
   background-color: white;
   border-radius: 10px;
   display: flex;
   flex-direction: column;
   justify-content: center;
   align-items: center;
   padding: 30px;
}

.message button {
  height: 48px;
  width: 163px;
  border-radius: 20.5px;
  background-color: #1ed1f2;
  outline: none;
  border: none;
  font-size: 18px;
  color: white;
  margin-top: 25px;
  cursor: pointer;
}

.message p {
  font-size: 24px;
}

.icon-message {
  width: 100px;
  margin-bottom: 15px;
}

